body {
  margin: 0;
  padding: 0;
}

.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__center {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 37.5vmin;
  left: 37.5vmin;
}

.stopwatch__watch-hand {
  transform-origin: bottom;
  position: absolute;
}

.stopwatch__watch-hand--minutes-hand {
  z-index: 0;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  top: 20vmin;
  left: 38.5vmin;
}

.stopwatch__watch-hand--seconds-hand {
  z-index: 1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  top: 2vmin;
  left: 39.25vmin;
}

.stopwatch--speed-up .stopwatch__watch-hand--minutes-hand {
  animation: 600s steps(60, end) infinite rotate;
}

.stopwatch--speed-up .stopwatch__watch-hand--seconds-hand {
  animation: 10s linear infinite rotate;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
